import React from 'react';
import {Row, Col} from 'react-bootstrap';
import PortfolioList from './portfoliolist/portfoliolist';
import PortfolioMain from './portfoliomain/portfoliomain';

class Portfolio extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      projects: []
    }
  }  

  componentDidMount() {
    fetch('projects.json')
    .then(response => response.json())
    .then(myjson => {
      this.setState({projects: myjson})
      });
    
  }

  render() {
    return (
        <Row>
            <Col xs={12} md={3}>
                <PortfolioList projects={this.state.projects}/>   
            </Col>
            <Col xs={12} md={9}>
                <PortfolioMain projects={this.state.projects}/>
            </Col>
       </Row>
    );
  }
}

export default Portfolio;